<template>
  <div class="categories__wrapper" id="all-categories">
    <div class="container">
      <div class="categories__header row">
        <ul class="list hide-scroll-bar">
          <li
            class="list__item"
            v-for="cate in categoriesInNav"
            :key="cate.cateegory_id"
          >
            <router-link
              class="link"
              active-class="link--active"
              :to="{ name: 'ViewAllCategories', hash: `#c${cate.category_id}` }"
              @click="() => hashClick(`c${cate.category_id}`)"
            >
              {{ cate.name }}
              <span class="active-indicator" />
            </router-link>
          </li>
        </ul>
      </div>

      <div class="categories__section row">
        <div class="col-xs-12 col-sm-8">
          <div
            class="categories__section-item"
            v-for="category in lv1Categories"
            :key="category.category_id"
          >
            <div class="categories__section-item-header">
              <h4 :id="`c${category.category_id}`">{{ category.name }}</h4>
            </div>
            <ul class="category__list row">
              <li
                class="category__item col-xs-6 col-sm-4 col-md-3 ellipsis"
                :title="item.name"
                v-for="item in category.children"
                :key="item.category_id"
              >
                <router-link
                  class="link"
                  active-class="link--active"
                  :to="item.url_path"
                >
                  {{ item.name }}
                </router-link>
              </li>
            </ul>
            <div class="categories__section-divider" />
          </div>
        </div>
        <div class="col-xs-12 col-sm-4"></div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import get from "@/util/get";
export default {
  data() {
    return {
      filterLength: null,
      gridCol: 4,
    };
  },
  computed: {
    ...mapGetters(["categoriesList"]),
    lv1Categories() {
      const { categoriesList } = this;
      return get(categoriesList, "children", []);
    },
    categoriesInNav() {
      const { categoriesList, filterLength } = this;
      let retCates = null;
      if (filterLength !== null) {
        retCates = get(categoriesList, "children", []).slice(0, filterLength);
        retCates.push({ name: "More Categories", category_id: -1010 });
      } else {
        retCates = get(categoriesList, "children", []);
      }
      return retCates;
    },
  },
  methods: {
    ...mapActions(["setNavBarTitle"]),
    hashClick(toId) {
      const el = document.getElementById(toId);
      el.scrollIntoView({
        behavior: "smooth",
      });
    },
  },
  mounted() {
    this.setNavBarTitle({
      en: "All categories",
      is: "Allir flokkar",
    });
  },
};
</script>

